import { cloneDeep, find } from 'lodash/fp'

/**
 * Retrieves the config options provided by the call plugin.
 * @method getCallConfig
 * @return {Object}
 */
export function getCallConfig (state) {
  return cloneDeep(state.config.call)
}

/**
 * Retrieves all calls in state.
 * @method getCalls
 * @param  {Object} state    Redux state.
 * @return {Array} Call objects.
 */
export function getCalls (state) {
  return cloneDeep(state.call.calls)
}

/**
 * Retrieves a call from state with a specific call ID.
 * @method getCallById
 * @param  {Object}    state  Redux state.
 * @param  {string}    callId The ID of the call to retrieve.
 * @return {Object} A call object.
 */
export function getCallById (state, callId) {
  return find(call => call.id === callId, getCalls(state))
}

/**
 * Retrieves custom parameters from a call with a specific call ID.
 * @method getCustomParametersById
 * @param  {Object}    state  Redux state.
 * @param  {string}    callId The ID of the call to retrieve.
 * @return {Object} A call object.
 */
export function getCustomParametersById (state, callId) {
  const call = find(call => call.id === callId, getCalls(state))
  if (call && call.customParameters) {
    return call.customParameters
  }
  return []
}

/**
 * Retrieves media devices available on the system.
 * @method getDevices
 * @param  {Object}    state  Redux state.
 * @return {Object}
 */
export function getDevices (state) {
  return cloneDeep(state.call.devices)
}

/**
 * Retrieves media support information.
 * @method getMediaInfo
 * @param  {Object} state Redux state.
 * @return {Object}
 */
export function getMediaInfo (state) {
  return cloneDeep(state.call.media)
}

/**
 * Retrieves audio bridge information.
 * @method getAudioBridges
 * @param  {Object} state Redux state.
 * @return {Object}
 */
export function getAudioBridges (state) {
  return cloneDeep(state.call.audioBridges)
}

/**
 * Retrieve all calls currently part of an audio bridge.
 * @method getBridgeCalls
 * @param state
 * @param bridgeId
 * @return {Array} List of calls currently part of the specified audio bridge.
 */
export function getBridgeCalls (state, bridgeId) {
  return cloneDeep(find(bridge => bridge.id === bridgeId, getAudioBridges(state)).calls)
}
