import { put } from 'redux-saga/effects'
// Events
import eventsMap from '../interface/events'
import { mapEvents } from '../../events/interface/actions'

// Import the interface to implement.
import pluginInterface from '../interface'

import { mwiReceived, fetchMwi } from './sagas'

/**
 * Plugin for voicemail service.
 * Provides the SDK with the Voicemail feature.
 */
export default function mwiLink () {
  function * init () {
    yield put(mapEvents(eventsMap))
  }

  const capabilities = ['voicemail']

  return {
    capabilities,
    api: pluginInterface.api,
    name: pluginInterface.name,
    reducer: pluginInterface.reducer,
    init,
    sagas: [mwiReceived, fetchMwi]
  }
}
