import { actionChannel, put, take, select } from 'redux-saga/effects'
// Auth
import { getConnectionInfo, getPlatform } from '../../auth/interface/selectors'
// Notifications
import { NOTIFICATION_RECEIVED } from '../../notifications/interface/actionTypes'
// MWI Actions
import * as actions from '../interface/actions'
import * as actionTypes from '../interface/actionTypes'
// Request
import request from '../../request/effects'
// Errors
import BasicError, { mwiCodes } from '../../errors'
// Constants
import { platforms } from '../../constants'

// Logs
import { logManager } from '../../logs'
const log = logManager.getLogger('MWI')

export function * mwiReceived () {
  const mwiEventChannel = actionChannel(
    action =>
      action.type === NOTIFICATION_RECEIVED &&
      action.payload.notificationMessage &&
      action.payload.notificationMessage.eventType === 'mwi'
  )
  const mwiReceived = yield mwiEventChannel
  while (true) {
    const action = yield take(mwiReceived)

    // Parse the mwi data
    const mwiData = {
      ...action.payload.notificationMessage.mwiNotificationParams
    }
    mwiData.newMessagesWaiting = mwiData.mwi === 'yes'
    delete mwiData.mwi
    log.info(`There are ${mwiData.newMessagesWaiting ? mwiData.newMessagesWaiting : 'no'} new messages waiting.`)
    mwiData.lastUpdated = action.payload.notificationMessage.time
    log.info(`There are ${mwiData.totalVoice} total voicemails. ${mwiData.unheardVoice} unheard.`)

    yield put(actions.mwiUpdate({ mwiData }))
  }
}

export function * fetchMwi () {
  while (true) {
    const action = yield take(actionTypes.FETCH_MWI)
    const connInfo = yield select(getConnectionInfo)
    const options = {
      ...action.payload,
      ...connInfo
    }

    const platform = yield select(getPlatform)
    const version = platform === platforms.UC ? 1 : options.server.version
    const response = yield request({
      url: `${options.server.protocol}://${options.server.server}:${options.server.port}/rest/version/${version}/user/${options.username}/voicemail`,
      method: 'GET'
    })

    if (response.error) {
      let error
      if (response.payload.body) {
        // Handle errors from the server.
        const { statusCode } = response.payload.body.mwiresponse
        const errMsg = `Failed to fetch voicemail(s). Status Code: ${statusCode}`
        log.info(errMsg)

        error = new BasicError({
          code: mwiCodes.FETCH_MWI_FAIL,
          message: errMsg
        })
      } else {
        // Handle errors from the request helper.
        const { message } = response.payload.result
        const errMsg = `Voicemail fetch request failed. ${message}`
        log.info(errMsg)

        error = new BasicError({
          code: mwiCodes.FETCH_MWI_FAIL,
          message: errMsg
        })
      }

      yield put(actions.mwiUpdate({ error }))
    } else {
      let mwiData = {}
      if (response.payload.body.mwiresponse.statusCode === 52) {
        // This statusCode means this user has absolutely no messages, new or old.
        // It is therefore accompanied with absolutely no data, so we make some for the store.
        mwiData = {
          lastUpdated: Date.now(),
          newMessagesWaiting: false,
          totalVoice: '0',
          unheardVoice: '0'
        }
        log.info('There are no new or old voicemails.')
      } else {
        mwiData = { ...response.payload.body.mwiresponse }
        delete mwiData.statusCode

        // Parse the mwi data
        mwiData.newMessagesWaiting = mwiData.mwi === 'yes'
        delete mwiData.mwi
        log.info(`There are ${mwiData.newMessagesWaiting ? mwiData.newMessagesWaiting : 'no'} new messages waiting.`)
        mwiData.lastUpdated = Date.now()
        log.info(`There are ${mwiData.totalVoice} total voicemails. ${mwiData.unheardVoice} unheard.`)
      }
      yield put(actions.mwiUpdate({ mwiData }))
    }
  }
}
