// Call History plugin.
import { api, name, reducer } from './interface'
import * as sagas from './sagas'
import events from './interface/events'

// Other plugins.
import { mapEvents } from '../events/interface/actions'

// Libraries.
import { values } from 'lodash/fp'
import { put } from 'redux-saga/effects'

/**
 * Call History plugin factory.
 * @method callHistory
 * @return {Object} plugin - An sdk plugin.
 */
export default function callHistory () {
  function * init () {
    yield put(mapEvents(events))
  }

  return {
    name,
    capabilities: ['callHistory'],
    init,
    api,
    reducer,
    sagas: values(sagas)
  }
}
