/**
 * This file is a plugin for the "clickToCall" feature. It is meant to connect two specified devices
 * Reference info: https://confluence.genband.com/display/KSDK/Plugins
 */
import pluginInterface from './interface'

// Sagas
import { clickToCallSaga } from './sagas'

// Events
import events from './interface/events'

// Other plugins.
import { mapEvents } from '../events/interface/actions'

// Libraries.
import { put } from 'redux-saga/effects'

/**
 * clickToCall Plugin (that is implemented using a saga).
 *
 * @method clickToCallImplementation
 * @return {Object} An instance of the "clickToCall" plugin.
 */
export default function clickToCallImplementation () {
  function * init () {
    // Map events
    yield put(mapEvents(events))
  }

  return {
    // Interface Components:
    name: pluginInterface.name,
    capabilities: ['clickToCall'],
    api: pluginInterface.api,
    reducer: pluginInterface.reducer,
    // Implementation Components
    sagas: [clickToCallSaga],
    init
  }
}
