// Auth plugin.
import * as actions from '../interface/actions'
import * as actionTypes from '../interface/actionTypes'
import { getAuthConfig, getConnectionInfo, getSubscriptionInfo } from '../interface/selectors'
import { refreshToken, createToken } from '../tokens/uc'
import { subscribe as gatewaySubscribe, unsubscribe, resubscribe } from '../subscription/requests'

// Other plugins.
import * as connectivityActionTypes from '../../connectivity/interface/actionTypes'
import { NOTIFICATION_RECEIVED } from '../../notifications/interface/actionTypes'
import { connectWebsocket, disconnectWebsocket } from '../../connectivity/interface/effects'
import { getConnectionState } from '../../connectivity/interface/selectors'
import { logManager } from '../../logs'

// Helpers.
import BasicError, { authCodes } from '../../errors'
import { DISCONNECT_REASONS } from '../constants'
import request from '../../request/effects'

// Libraries.
import {
  call,
  put,
  select,
  take,
  fork,
  cancel,
  cancelled,
  race,
  actionChannel,
  delay,
  takeEvery
} from 'redux-saga/effects'

// Constants
import { platforms } from '../../constants'

const platform = platforms.UC
const log = logManager.getLogger('AUTH')

/**
 * Top level saga that acts as an "entry point" for authentication.
 * Handles the flow for connecting and disconnecting users.
 * @method connectFlow
 */
export function * connectFlow () {
  while (true) {
    // Wait for a CONNECT action.
    const action = yield take(actionTypes.CONNECT)

    // Fork a non-blocking task for the connection process.
    const task = yield fork(connect, action)

    // Take the actions for if an error or disconnect occurs.
    const finishOrError = yield take([actionTypes.CONNECT_FINISHED, actionTypes.DISCONNECT])

    if (finishOrError.type === actionTypes.DISCONNECT) {
      // If disconnect was called before connect finished, then cancel the connect.
      yield cancel(task)
    } else if (finishOrError.type === actionTypes.CONNECT_FINISHED && finishOrError.error) {
      // If an error occurred during connection, then connect flow resets
      continue
    } else if (finishOrError.type === actionTypes.CONNECT_FINISHED && !finishOrError.error) {
      // If connection finished successfully, then wait for a disconnect.
      const disconnectAction = yield take([actionTypes.DISCONNECT, actionTypes.DISCONNECT_FINISHED])

      // if disconnect has finished, we dont need to do a teardown of auth state, or disconnect the websocket, so reset connectFlow
      if (disconnectAction.type === actionTypes.DISCONNECT_FINISHED) {
        continue
      }

      yield call(disconnect)
    }
  }
}

/**
 * Handles the connection process for username/password and token scenarios.
 * @method connect
 * @param  {Object} action A CONNECT action.
 */
export function * connect (action) {
  const { credentials, options } = action.payload

  // Retrieve the connection info.
  const config = yield select(getAuthConfig)

  let tokenInfo
  if (credentials.username && credentials.password) {
    log.info('Attempting to connect using Username / Password')
    // If this is a username/password connect scenario, we need to generate
    //      the token ourselves.
    tokenInfo = yield call(createToken, action, config)

    if (tokenInfo.error) {
      // If token generation failed, stop the connection process.
      yield put(actions.connectFinished(tokenInfo))
      return
    }
  } else if (credentials.accessToken) {
    log.info('Attempting to connect using an AccessToken')
    // In a token scenario, all the information is provided to us.
    tokenInfo = {
      accessToken: credentials.accessToken,
      refreshToken: credentials.refreshToken,
      tokenExpires: credentials.expires,
      username: credentials.username
    }
  } else if (!credentials.accessToken && credentials.refreshToken && credentials.username) {
    log.info('Attempting to connect using a RefreshToken')
    // The user is attempting to log in with a refreshToken (and username).

    tokenInfo = yield call(refreshToken, action, config)

    if (tokenInfo.error) {
      // If token generation failed, stop the connection process.
      log.info(
        'Something went wrong while attempting to create an accessToken from the supplied refreshToken. Error: ',
        tokenInfo.error
      )
      yield put(actions.connectFinished(tokenInfo))
      return
    }
  } else if (credentials.oauthToken && credentials.username) {
    /**
     * OAuth token handling
     * Unlike the conventional authToken method, we won't be making use of the refreshToken
     */

    log.info('Setting Connection with OAuth in UC')
    tokenInfo = {
      oauthToken: credentials.oauthToken,
      username: credentials.username
    }
  } else {
    log.info('The login signature is not valid.')
    // Other connect scenarios not supported.
    yield put(
      actions.connectFinished({
        error: new BasicError({
          message: 'This login signature is not valid.',
          code: authCodes.INVALID_CREDENTIALS
        })
      })
    )
    return
  }

  try {
    // Use the token to subscribe.
    yield call(subscribe, config, tokenInfo, options)
  } catch (error) {
    log.info('Failed to connect. Error: ', error)
    yield put(actions.connectFinished({ error }))
  } finally {
    if (yield cancelled()) {
      log.info('Disconnecting.')
      yield call(disconnect)
    }
  }
}

/**
 * This subscribe function makes a request to the UC gateway for a subscription.
 * If successful it triggers a connectFinished action containing subscription and connection information.
 * @method subscribe
 * @param  {Object}    config       Config values.
 * @param  {Object}    tokenInfo
 * @param  {string}    tokenInfo.username     Username passed in by user.
 * @param  {string}    tokenInfo.accessToken  Access Token either from the user or the server.
 * @param  {string}    tokenInfo.refreshToken Refresh Token either from the user or the server.
 * @param  {number}    tokenInfo.tokenExpires Expires time for the access token.
 * @param  {Object}    [options] Options provided as part of the API.
 * @param  {boolean}   [options.forceLogout]
 * @param  {string}    [options.clientCorrelator]
 */
export function * subscribe (config, tokenInfo, options) {
  const { username, accessToken, refreshToken, tokenExpires, oauthToken } = tokenInfo

  let requestOptions = {}
  // UC requires a token attached to every request.
  if (oauthToken && !accessToken) {
    // For subscription requests using an OAuth Token, attach it as a header.
    requestOptions = {
      headers: {
        Authorization: `Bearer ${oauthToken}`
      }
    }
  } else {
    // For a usual token, attach it to the URL.
    requestOptions = {
      queryParams: {
        token: accessToken
      }
    }
  }

  const connection = {
    ...config.subscription,
    // Hardcode the version number for SPiDR requests, since it is
    //      different than the Gateway version number in configs.
    version: '1'
  }

  const connectionInfo = {
    // Information for other plugins to make requests.
    server: {
      protocol: config.subscription.protocol,
      server: config.subscription.server,
      port: config.subscription.port,
      version: config.subscription.version
    },
    accessToken,
    oauthToken,
    refreshToken,
    tokenExpires,
    username,
    // Request options that all other plugins will use.
    requestOptions
  }
  const userInfo = {
    username,
    accessToken,
    refreshToken
  }
  yield put(
    actions.setConnectionInfo(
      {
        connection: connectionInfo,
        userInfo
      },
      platform,
      false
    )
  )

  /*
   * Merge the API options together with the requestOptions. This doesn't make
   *    sense, but this is how the `gatewaySubscribe` function takes these
   *    parameters.
   */
  const extras = {
    ...options,
    ...requestOptions
  }

  log.info(`Subscribing user ${username}.`)
  const response = yield call(gatewaySubscribe, connection, { username }, extras)

  // If the subscription failed, dispatch an error action and stop here.
  if (response.error) {
    log.info('Failed to subscribe user. Error: ', response.error)
    yield put(actions.connectFinished(response))
    return
  }

  // WS stuff
  const subscription = {
    ...response.subscriptionParams,
    servicesInfo: response.servicesInfo,
    url: response.subscription
  }

  const params = {}

  if (oauthToken && !accessToken) {
    log.debug('We have an OAuth token but no Access token. Adding OAuth token to parameters as "access_token"')
    // If using OAuth token, set the param token property to its value
    params.access_token = oauthToken
  } else {
    // Otherwise, use the normal access token
    params.token = accessToken
  }
  const websocketInfo = {
    ...config.websocket,
    url: subscription.notificationChannel,
    params: params
  }

  log.info('Connecting to websocket.')
  // Request the websocket connection.
  const wsOpenOrError = yield connectWebsocket(websocketInfo, platform)

  if (wsOpenOrError.error) {
    log.info('Failed to connect to websocket. Error: ', wsOpenOrError.error)
    yield put(
      actions.connectFinished({
        error: new BasicError({
          message: 'Websocket connection failed.',
          code: authCodes.CONNECT_FAIL_WS_ERROR
        })
      })
    )
  } else {
    // Filter out null values from subscribed services before storing in state.
    subscription.service = subscription.service.filter(service => service !== null)
    log.info('Subscribed to following services: ', subscription.service)

    yield put(
      actions.connectFinished(
        {
          connection: connectionInfo,
          userInfo,
          // Information about the current subscription.
          subscription
        },
        platform,
        false
      )
    )

    if (refreshToken) {
      log.debug('Refreshing token')
      yield fork(refreshTokenSaga)
    }
  }
}

/**
 * Saga that handles a "subscription gone" notification.
 * Treat the notification as a forced disconnect.
 * @method onSubscriptionGone
 */
export function * onSubscriptionGone () {
  // Redux-saga take() pattern.
  // Take notifications about the subscription being 'gone'.
  function takeGoneNotification (action) {
    return (
      action.type === NOTIFICATION_RECEIVED &&
      action.payload.notificationMessage &&
      action.payload.notificationMessage.eventType === 'gone'
    )
  }

  // Take "resubscription failed" actions caused by the subscription not existing.
  function takeResubGoneSubscription (action) {
    return (
      action.type === actionTypes.RESUBSCRIPTION_FINISHED &&
      action.error &&
      // Magic number: 4 is the statusCode KL/CIM returns when a resub request
      //    fails with a 404.
      action.payload.code === 4
    )
  }

  while (true) {
    const action = yield take([takeGoneNotification, takeResubGoneSubscription])
    // Dispatch an action to disconnect the websocket (and let the connectivity
    //      plugin know we expect it to be disconnected).
    const wsState = yield select(getConnectionState, platform)
    if (wsState.connected) {
      yield disconnectWebsocket(undefined, platform)
    }

    // Dispatch a disconnect finished action to trigger "user disconnected" logic.
    const content =
      action.payload.notificationMessage && action.payload.notificationMessage.retryAfter
        ? { reason: DISCONNECT_REASONS.GONE, retryAfter: action.payload.notificationMessage.retryAfter }
        : { reason: DISCONNECT_REASONS.GONE }
    yield put(actions.disconnectFinished(content))
  }
}

/**
 * Saga for extending a user's subscription.
 * When triggered, make a resub request to SPiDR for a specific subscription.
 * @method extendSubscription
 */
export function * extendSubscription () {
  // TODO: Refactor and combine with Link subscription logic. Parameterize everything.
  // Re-subscription timer is triggered by a successful connect or a previous resub finishing.
  // Use a channel to queue the triggers (so it can catch the put resub action at the end).
  const resubTriggers = yield actionChannel([actionTypes.CONNECT_FINISHED, actionTypes.RESUBSCRIPTION_FINISHED])
  while (true) {
    const action = yield take(resubTriggers)

    // If the action was a connection error, ignore it.
    if (action.type === actionTypes.CONNECT_FINISHED && action.error) {
      continue
    }

    const subscription = yield select(getSubscriptionInfo, platform)

    let attemptNum
    // If the action was a failed resubscription, increment the attempt number.
    if (action.type === actionTypes.RESUBSCRIPTION_FINISHED && action.error) {
      attemptNum = action.payload.attemptNum + 1
    } else {
      attemptNum = 1
    }

    // Set the delay to be halfway between now and when the subscription expires.
    let resubDelay = (subscription.expires * 1000) / Math.pow(2, attemptNum)
    // Don't try to resub more often than every 5 minutes.
    resubDelay = resubDelay > 30000 ? resubDelay : 30000

    // Wait for either the resub delay or a disconnect action.
    const { expiry } = yield race({
      expiry: delay(resubDelay),
      disconnect: take(actionTypes.DISCONNECT_FINISHED)
    })

    // If the resubDelay expired, attempt resubscription
    if (expiry) {
      log.info('Extending user subscription.')
      const connection = yield select(getConnectionInfo, platform)
      const subscription = yield select(getSubscriptionInfo)

      const response = yield call(resubscribe, connection, subscription)

      if (response.error) {
        log.info('Failed to resubscribe user. Error: ', response.error)
        yield put(
          actions.resubscribeFinished(
            {
              error: response.error,
              attemptNum
            },
            platform
          )
        )
      } else {
        log.info('Successfully resubscribed user.')
        // TODO: Check response.serviceInfo for full/partial resubscription.
        yield put(actions.resubscribeFinished({ attemptNum }, platform))
      }
    }
  }
}

/**
 * The logic for disconnecting from the server. This includes token revocation.
 * @method disconnect
 */
export function * disconnect () {
  const connection = yield select(getConnectionInfo, platform)

  const wsState = yield select(getConnectionState, platform)
  if (wsState.connected) {
    log.info('Disconnecting websocket.')
    // Dispatch a WS disconnect action and wait for a response
    yield disconnectWebsocket(undefined, platform)
  }

  const subscription = yield select(getSubscriptionInfo)

  log.info('Unsubscribing user.')
  yield call(unsubscribe, connection, subscription.url)

  log.info('Successfully unsubscribed user.')
  yield put(actions.disconnectFinished())
}

/**
 * The logic for refreshing accessToken / refreshToken. Adds new tokens to the store.
 * @method refreshTokenSaga
 */
function * refreshTokenSaga () {
  while (true) {
    const connection = yield select(getConnectionInfo, platform)
    const authConfig = yield select(getAuthConfig)
    const { protocol, server, version, port } = connection.server

    const { disconnect } = yield race({
      disconnect: take(actionTypes.DISCONNECT_FINISHED),
      expiry: delay((connection.tokenExpires - authConfig.earlyRefreshMargin) * 1000)
    })

    // On disconnection we need to end this saga. Since it is not a root saga, it will get re-triggered
    // after the next connection.
    if (disconnect) {
      return
    } else {
      log.info('Refreshing user token.')
      const response = yield request({
        url: `${protocol}://${server}:${port}/${version}/auth/refresh_token`,
        queryParams: {
          token: connection.refreshToken,
          expires: connection.tokenExpires
        }
      })

      if (response.error) {
        const info = response.payload.body ? response.payload.body : response.payload.result
        log.info(
          `Attempt to refresh the accessToken failed. Error: ${response.error} This may be due to server or permissions issues. Info: `,
          info
        )
        yield put(actions.disconnect())
        return
      } else {
        log.info('Successfully refreshed user token.')
        const {
          access_token: accessToken,
          refresh_token: refreshToken,
          expires: tokenExpires
        } = response.payload.body.success
        yield put(
          actions.refreshTokensFinished(
            {
              connection: {
                accessToken,
                refreshToken,
                tokenExpires,
                // Ensure the extra options for requests is updated
                //      with the new token.
                requestExtras: {
                  queryParams: {
                    token: accessToken
                  }
                }
              }
            },
            platform
          )
        )
      }
    }
  }
}
/**
 * The logic for updating the store's tokens by the user.
 * @method updateTokenSaga
 */
export function * updateTokenSaga () {
  const tokenChannel = yield actionChannel([actionTypes.REFRESH_TOKENS])
  while (true) {
    const action = yield take(tokenChannel)

    const { username, accessToken, oauthToken } = action.payload.credentials

    let requestOptions = {}

    if (oauthToken && username && !accessToken) {
      // For subscription requests using an OAuth Token, attach it as a header.
      requestOptions = {
        headers: {
          Authorization: `Bearer ${oauthToken}`
        }
      }
    } else if (accessToken && username && !oauthToken) {
      // Ensure the extra options for requests is updated with the new token.
      requestOptions.queryParams = {
        token: action.payload.credentials.accessToken
      }
    } else {
      log.debug('updateToken called without the necessary parameters')
    }

    log.info('Refreshing token for platform: ', platform)
    yield put(
      actions.refreshTokensFinished(
        {
          connection: {
            ...action.payload.credentials,
            requestOptions
          }
        },
        platform
      )
    )
  }
}

/**
 * Triggers onConnectionLost saga when a WS_RECONNECT_FAILED actionType occurs
 * @method onConnectionLostEntry
 */
export function * onConnectionLostEntry () {
  yield takeEvery(connectivityActionTypes.WS_RECONNECT_FAILED, onConnectionLost)
}

/**
 * Handles lost connections from the connectivity plugin
 * @method onConnectionLost
 */
export function * onConnectionLost () {
  yield put(actions.disconnectFinished({ reason: DISCONNECT_REASONS.LOST_CONNECTION }))
}
