// clickToCall plugin.
import * as actionTypes from './interface/actionTypes'
import * as actions from './interface/actions'
import { getConnectionInfo, getPlatform } from '../auth/interface/selectors'

// Libraries.
import { put, take, select } from 'redux-saga/effects'
import { logManager } from '../logs'

// Other plugins.
import request from '../request/effects'

// Error handling
import BasicError, { clickToCallCodes } from '../errors'
import { platforms } from '../constants'

// Get the logger
const log = logManager.getLogger('CLICKTOCALL')

export function * clickToCallSaga () {
  while (true) {
    // wait for CLICK_TO_CALL action dispatch.
    const action = yield take(actionTypes.CLICK_TO_CALL)

    // ensure both caller and callee are provided in payload.
    if (!action.payload.caller || !action.payload.callee) {
      log.info('Missing call participant information')
      yield put(
        actions.clickToCallFinish({
          payload: {
            error: new BasicError({
              message: 'callee or caller were not provided in CLICK_TO_CALL action payload',
              code: clickToCallCodes.MISSING_ARGS
            }),
            callId: action.payload.callId
          }
        })
      )
      continue
    }

    const conn = yield select(getConnectionInfo)
    const platform = yield select(getPlatform)

    const { server, username } = conn

    const version = platform === platforms.UC ? 1 : server.version

    const url = `${server.protocol}://${server.server}:${server.port}/rest/version/${version}/user/${username}/clicktocall`

    const data = {
      clickToCallRequest: {
        callingParty: action.payload.caller,
        calledParty: action.payload.callee
      }
    }

    const options = {
      url,
      method: 'POST',
      body: JSON.stringify(data)
    }

    const requestTime = new Date().getTime()
    // wait until we get a response from /clicktocall service
    const response = yield request(options)

    // determine what type of response was received.
    if (response.error) {
      yield put(
        actions.clickToCallFinish({
          payload: {
            error: new BasicError({
              message: response.payload.result.message,
              code: clickToCallCodes.RESPONSE_ERROR
            }),
            callId: action.payload.callId
          }
        })
      )
    } else {
      yield put(
        actions.clickToCallFinish({
          callId: action.payload.callId,
          caller: action.payload.caller,
          callee: action.payload.callee,
          requestTime
        })
      )
    }
  }
}
